/**
   Component: button-extra.scss
*/

// Standard buttons redesign

.btn {
  -webkit-appearance: none;
  outline: none !important;
  @include transition(all .1s);

  &.btn-link {
    @include box-shadow(0 0 0 #000);
    border: 0;
  }
  &.btn-default {
    border-color: $btn-default-border;
  }

  .input-group .input-sm + .input-group-btn & {
    font-size: 13px;
  }
  &:active,
  &.active,
  &:hover, &:focus {
    box-shadow: 0 3px 10px rgba(0,0,0,0.23),0 3px 10px rgba(0,0,0,0.16);
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus,
    &.focus,
    &:active,
    &.active {
      box-shadow: 0 0 0 #000;
    }
  }
}

.btn-group.open .dropdown-toggle {
  box-shadow: 0 3px 10px rgba(0,0,0,0.23),0 3px 10px rgba(0,0,0,0.16);
}

.btn-group-small .btn {
  padding: 10px;
  margin-bottom: 5px;
  font-size: 11px;
}

// Button Image
// -----------------------------------

.btn-image {
  padding: 0 !important;
  border: 0;
  &:hover, &:focus {
    box-shadow: 0 3px 10px rgba(0,0,0,0.23),0 3px 10px rgba(0,0,0,0.16);
  }
}

//
// Extension for Bootstrap buttons
// ------------------------------------

// New colors

.btn-inverse   { @include button-variant(#fff, $brand-inverse, $brand-inverse); }
.btn-amber     { @include button-variant(#fff, $brand-amber, transparent); }
.btn-purple    { @include button-variant(#fff, $brand-purple, transparent);    }
.btn-pink      { @include button-variant(#fff, $brand-pink, transparent);      }
.btn-orange    { @include button-variant(#fff, $brand-orange, transparent);    }

.btn-outline {
  background-color: transparent;
  border-color: #fff;
  &:hover, &:focus {
    background-color: #fff;
    color: $brand-primary;
  }
}

// Flat square button

.btn-flat {
  border-bottom-width: 1px;
  border-radius: 0;
  box-shadow: 0 0 0 #000;
}

// Extra size

.btn-xl {
  padding: 20px 16px;
  font-size: 18px;
}

// Button square

.btn-square {
  border-radius: 0;
}


// Button pill

.btn-pill-left {
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
  padding-left: 18px;
}

.btn-pill-right {
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
  padding-right: 18px;
}

// Rounded buttons

.btn-oval {
  @extend .btn-pill-right;
  @extend .btn-pill-left;
}

// Labels for buttons
// --------------------------------------------------

.btn-labeled {
  padding-top: 0;
  padding-bottom: 0;
}

// BUTTONS LABEL mixin
// --------------------------------------------------

@mixin button-label-size($padding-vertical, $padding-horizontal, $border-radius) {
  padding: $padding-vertical $padding-small-horizontal;
  left: (-1 * $padding-horizontal);
  border-radius: ($border-radius - 1px) 0 0 ($border-radius - 1px);

  &.btn-label-right {
    left: auto;
    right: (-1 * $padding-horizontal);
    border-radius: 0 ($border-radius - 1px) ($border-radius - 1px) 0;
  }
}

.btn-label {
  position: relative;
  display: inline-block;
  min-width: 30px;
  background: transparent;
  @include button-label-size($padding-base-vertical, $padding-base-horizontal, $border-radius-base);
  &:after {
    content: "";
    position: absolute;
    top: 2px;
    bottom: 2px;
    right: 0;
    width: 1px;
    background: rgba(0, 0, 0, 0.1);
  }
}

.btn-lg .btn-label {
  @include button-label-size($padding-large-vertical, $padding-large-horizontal, $border-radius-large);
}
.btn-sm .btn-label {
  @include button-label-size($padding-small-vertical, $padding-small-horizontal, $border-radius-small);
}
.btn-xs .btn-label {
  @include button-label-size(1px, 5px, $border-radius-small);
}

// Fixed width buttons
// --------------------------------------------------

$btn-fw-sm:       80px;
$btn-fw:          120px;
$btn-fw-lg:       150px;

.btn-fw {
  min-width: $btn-fw;
  &.btn-sm {
    min-width: $btn-fw-sm;
  }
  &.btn-lg {
    min-width: $btn-fw-lg;
  }

}


// Button Circle
// -----------------------------------

.btn-circle {
  width: 35px;
  height: 35px;
  text-align: center;
  padding: 6px 0;
  font-size: 12px;
  line-height: 1.428571429;
  border-radius: 500px;
}
.btn-circle.btn-lg {
  width: 50px;
  height: 50px;
  padding: 10px 16px;
  font-size: 18px;
  line-height: 1.33;
  border-radius: 25px;
}
.btn-circle.btn-xl {
  width: 70px;
  height: 70px;
  padding: 10px 16px;
  font-size: 24px;
  line-height: 1.33;
  border-radius: 35px;
}


// Navbar button offset

.btn-offset {
  position: absolute;
  right: 60px;
  height: 35px;
  z-index: 99999;
  top: $header-hg - 30;
  @include translate3d(0,0,0);
  .layout-fixed,
  .layout-material {
    position: fixed;
  }
}
